import React from "react";
import "./about.css";
import imgs from '../img/languages.png'

function About (){
    return(
        <div className="a">
            <div className="a-left">
            <div className="a-card bg"></div>
                <div className="a-card">
                    <img src={imgs} alt="" className="a-img"></img>
                </div>
            </div>
            <div className="a-right">
                <h1 className="a-title">About Me</h1>
                <p className="a-sub">
                    I began my journey in Computer Science in 2019 at The University of KwaZulu Natal
                    Over time I developed a keen interest in solving problems faced by people through the
                    use of computers and technology.

                </p>
                <p className="a-desc">
                    Here are some links:
                    <br/>
                    <a href="https://github.com/MegladonCodes">My GitHub!</a>
                    <br/>
                    <a href="https://www.linkedin.com/in/mikhail-govender-a0baba205 ">My LinkedIn </a>
                </p>
            </div>
        </div>
    )
}

export default About