import React from 'react'
import "./MainComponent.css"
import imgs from '../img/coder.png'
function Main (){
    return(
        <div className = 'i'>
            <div className='i-left'>
                <div className="i-left-wrapper">
                    <h2 className='i-intro'>Hello, My name is</h2>
                    <h1 className='i-name'>Mikhail Govender</h1>
                    <div className='i-title'>
                        <div className='i-title-wrapper'>
                            <div className='i-title-item'>Web Developer</div>
                            <div className='i-title-item'>Desktop Developer</div>
                            <div className='i-title-item'>UI Designer</div>
                            <div className='i-title-item'>UX Designer</div>
                        </div>
                    </div>
                    <p className='i-desc'>
                        I am a final year Computer Science and IT student from UKZN.
                        I have experience developing business desktop applications, as
                        well as websites using C# and ASP.NET.
                        <br/>
                        <br/>
                        I enjoy designing beautiful looking user interfaces as well and spend most
                        of my time drinking good coffee.
                        <br/>
                        <br/>
                        This is my first time using React! so I sincerely hope you love this site!
                        
                    </p>

                </div>
                
            </div>
            <div className='i-right'>
                <div className='i-bg'></div>
                <img src={imgs} alt='' className="i-img"/>
            </div>
        </div>
    )
}

export default Main;